 <form v-if="complete_transaction" @submit.prevent="completeTransaction(item.id)">
    <div class="input-group container"  >
  <input type="text" class="form-control"
   placeholder="Enter Any remark "
    v-model="remark">
  <button 
  class="btn btn-primary me-2" type="submit" :disabled="loading">
  <span  v-if="loading" v-html="loadingSpinner()"></span>
  <span v-else>Save</span>
  </button>
</div>
 </form>


 

        <div class="modal-body">

<div class="text-center mb-2">
  <div class="btn-group mb-2">
    <button
      class="btn btn-secondary me-2"
      @click="changeStatus(item.id,3)"
      :disabled="spinner"
      v-if="user_permissions.includes('is_programmer') && item.status?.name === 'paid'"
    >
      Mark Approved
    </button>

    <button
      class="btn btn-success"
      @click="fillReceipt =! fillReceipt"
      :disabled="spinner"
      v-if="user_permissions.includes('can_complete_transaction') && item.status?.name === 'paid'"
    >
      Attach Receipt
    </button>
  </div>

  <div v-if="fillReceipt" class="d-flex justify-content-center align-items-end gap-2">
    <input
      class="form-control w-25"
      type="file"
      @change="handleFileUpload"
      accept="application/pdf"
      required
    >
    <input type="date" v-model="receipt_date" class="form-control w-25">

    <button class="btn btn-primary" @click="submitReceipt" :disabled="spinner">
      <span v-if="spinner" v-html="loadingSpinner()"></span>
      <span v-else>Submit</span>
    </button>
  </div>
</div>

          <div class="d-flex justify-content-center mb-2"  v-if="((user_role_id == awaiter.role_id && awaits !=='no one')) || (user_permissions.includes('is_md') && item.status?.name !== 'paid') || (user_role_id == item.last_checker_id && awaits ==='no one')">    
    <div class="btn-group" >



            <button class="btn btn-secondary me-2" @click="changeStatus(item.id,2)" :disabled="spinner" v-if="user_permissions.includes('is_md') || user_permissions.includes('is_programmer')">
              Unapprove
            </button>

            <button class="btn btn-primary me-2" @click="approval_status = 3" :disabled="spinner">
              Check 
            </button>
            <button class="btn btn-danger me-2" @click="approval_status = 4" :disabled="spinner">
              Reject
            </button>

            <button class="btn btn-info me-2" @click="approval_status = 14" :disabled="spinner">
              Remark
            </button>

            
            <button class="btn btn-warning" @click="approval_status = 17" :disabled="spinner">
              On Hold
            </button>



          </div>
            </div>
   
      <!-- <p class="text-center">{{ status == 3 ? 'Enter Approval Remark' : status == 4 ? 'Enter Rejection Remark' : 'Enter Remark' }}</p> -->
      <div v-if="approval_status" class="mb-2 text-center">
        <span
          class="badge rounded-pill"
          :class="{
            'bg-danger': approval_status == 4,
            'bg-success': approval_status == 3,
            'bg-info': approval_status == 14,
            'bg-warning': approval_status == 17
          }"
          style="font-size: 1rem;"
        >
          {{
            approval_status == 3
              ? 'Check / Approve Request'
              : approval_status == 4
              ? 'Reject Request'
              : 'Fill Remark'
          }}
        </span>
      </div>

<div class="input-group"  v-if="approval_status">
  <input type="text" class="form-control"
    :placeholder="approval_status == 3 ? 'Enter Approval Remark' : approval_status == 4 ? 'Enter Rejection Remark' : 'Enter Remark'" 
    v-model="remark">
  <button 
     @click="onApproveClick()"
  class="btn btn-primary me-2" type="button" :disabled="spinner">
  <span v-if="spinner" v-html="loadingSpinner()"></span>
  <span v-else>Save</span>
  </button>
</div>


       
      
   
        <div class="custom-table table3 table-responsive">
