                  {{ chain.name }} <br/>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td
                  v-for="(role, idx) in approvalChain"
                  :key="role.role_id || role.position || idx"
                  :class="role?.status?.color ? 'text-' + role.status.color : 'text-danger'"
                >
                <span> {{ role?.status?.name ?? 'Unchecked' }}</span><br/>
                <span class="text-dark" v-if="role.approved_at">Checked At  :{{ formatDate(role.approved_at) }} </span>  <br>
                <span class="text-dark" v-if="role.remark">Remark :  {{ role.remark }}</span> 
                </td>
              </tr>
            </tbody>
          </table>
