            <span v-if="reqItem.selected"> <br>Requesting : {{ reqItem.requesting_quantity }} </span>
            <input v-if="edit_item == reqItem.id" type="number" class="form-control" placeholder="Quantity" step="any" v-model="reqItem.quantity">
          </td>
