            {{ reqItem.rate }}
          <input v-if="edit_item == reqItem.id" type="number" class="form-control" placeholder="Quantity" step="any" v-model="reqItem.rate_original">

          </td>
          <td>

            <span>
              <!-- {{ reqItem.total }}<br> -->
              {{ formatNumber(reqItem.quantity * reqItem.rate_original) }}<br>
            </span>
            <span v-if="reqItem.selected">
                Requesting : {{ formatNumber(reqItem.requesting_quantity * reqItem.rate_original) }}
            </span>

          </td>
          <td>{{ reqItem.receiver ?? reqItem.beneficiary }}</td>
          <td  
          v-if="create_approval"
          class="text-center" >
                <svg
     v-if="reqItem.selected"
      @click="reqItem.selected =! reqItem.selected"
      xmlns="http://www.w3.org/2000/svg" width="26" height="26"
      fill="currentColor" class="bi bi-check2-circle" viewBox="0 0 16 16">
      <path d="M2.5 8a5.5 5.5 0 0 1 8.25-4.764.5.5 0 0 0 .5-.866A6.5 6.5 0 1 0 14.5 8a.5.5 0 0 0-1 0 5.5 5.5 0 1 1-11 0"/>
      <path d="M15.354 3.354a.5.5 0 0 0-.708-.708L8 9.293 5.354 6.646a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0z"/>
    </svg>

    <svg
    v-else
     @click="reqItem.selected =! reqItem.selected"
      xmlns="http://www.w3.org/2000/svg" width="26" height="26"
      fill="currentColor" class="bi bi-circle" viewBox="0 0 16 16">
      <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14m0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16"/>
    </svg>
        <input v-if="reqItem.selected" type="number" placeholder="enter quantity" class="form-control w-75" step="any"
         v-model="reqItem.requesting_quantity"
          @input="reqItem.requesting_quantity = enforceMax(reqItem.requesting_quantity,(reqItem.quantity - reqItem?.requested_sum).toFixed(2))"
         >

          <input v-if="reqItem.selected" type="text" placeholder="enter Description" class="form-control w-75 mt-1" v-model="reqItem.request_description">
          <input v-if="reqItem.selected" type="text" placeholder="enter Beneficiary" class="form-control w-75 mt-1"   v-model="reqItem.receiver">

          </td>
          <td>
                      <router-link  :to="`/assets/fuel-statement/${reqItem.requisition_items.asset_id}`"  v-if ="reqItem.requisition_items.asset_id" class="me-2">     
                            <button class="btn btn-outline-primary me-2">Diesel </button>
                      </router-link>  

                       <router-link  :to="`/requisition-budget/${reqItem.requisition_items.item_id}`"  v-if ="item.requisition_type ==='budget'">     
                            <button class="btn btn-outline-primary me-2">Budget</button>
                      </router-link>  
                        <router-link  :to="`/jobcard/${item.source_id}/${reqItem.requisition_items.asset_id}`"  v-if ="item.requisition_type ==='jobcard'" class="me-2">     
                            <button class="btn btn-outline-primary me-2">Jobcard</button>
                      </router-link>  
                         <router-link  :to="`/subcontractor/${approval.subcontractor_agreement_id}`"  v-if ="approval.subcontractor_agreement_id">     
                            <button class="btn btn-outline-primary me-2">Subcontractor</button>
                      </router-link>  

                      <button
                       v-if="user_permissions.includes('can_print_lpo') && (item.status.name === 'approved' || item.status.name === 'paid')  "
                      class="btn btn-outline-success me-2"
                      @click="printOrder(reqItem,item)"
                      >
                          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
  </svg>

                      </button>
                      <button
                      v-if="item.status?.name === 'approved' && user_permissions.includes('can_complete_transaction')"
                       class="btn btn-primary"
                          data-bs-toggle="modal" data-bs-target="#payment-proof"
